.main-nav {
  width: 100%;
  padding-top: 80px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }

.main-nav__toggle {
  display: none; }

.main-nav__toggle,
.main-nav__toggle-trigger,
.main-nav__toggle-burger {
  width: 80px;
  height: 80px;
  position: absolute;
  transition: 0.2s ease;
  cursor: pointer;
  z-index: 1; }

.main-nav__toggle-trigger {
  top: 0;
  left: -26px; }

.main-nav__toggle-burger {
  top: 36px;
  left: 9px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: -12px 0 #ffffff, 12px 0 #ffffff, 12px 12px #ffffff, -12px -12px #ffffff, 12px -12px #ffffff, -12px 12px #ffffff; }
  .main-nav__toggle-burger::before {
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #ffffff;
    content: "";
    top: -12px;
    left: 0; }
  .main-nav__toggle-burger::after {
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #ffffff;
    content: "";
    top: 12px;
    left: 0; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger {
  transform: rotate(180deg);
  box-shadow: 12px 12px #ffffff, -12px -12px #ffffff, 12px -12px #ffffff, -12px 12px #ffffff;
  transition: transform 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::before {
  transform: rotate(45deg) translateX(-5px);
  display: none;
  transition: transform 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::after {
  transform: rotate(-45deg) translateX(-5px);
  display: none;
  transition: transform 0.2s ease; }

.main-nav__additional-list {
  display: none; }

/*.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger + .main-nav__additional-list {
  animation: checked-anim 1s ease both;
}*/
.main-nav__toggle:checked + .main-nav__toggle-trigger ~ .main-nav__additional-list {
  width: 100%;
  display: flex; }

.main-nav__toggle:checked + .main-nav__toggle-trigger ~ .main-nav__list {
